import React,{useState,useEffect} from 'react'
import SapQEdit from './SapQEdit'
import SelectModule from './SelectModule'


function Qadmin() {


const [toggleMode, setToggleMode] = useState(true)
const [menu, setMenu] = useState(false)
const [module, setModule] = useState("fico")

    

function toggleMenu(){
    setMenu(!menu)
}

function currentModule(m){
    setModule(m)
}
    return (
        menu?<SapQEdit toggleMenu={toggleMenu} module={module}/>:<SelectModule toggleMenu={toggleMenu} currentModule={currentModule}/>
    )}

export default Qadmin
