import React,{useState, useEffect, useRef} from 'react'

function SapQEdit( {toggleMenu, module}) {
    const [currentQ, setCurrentQ] = useState({Q:"What is the purpose of period interval 3 when you define open and closed periods?",OPS:["Posting to special purpose ledger"," Posting from CO to FI"," Posting to special periods"," Authorization control for posting periods",""],ANS:"B"})
    const [idsList, setIdsList] = useState([])
    const [currentQnum, setCurrentQnum] = useState(1)
    const [toggleMode, setToggleMode] = useState(true)
    const [searchQnum, setSearchQnum] = useState(currentQnum)
   
    
    
    
    
        async function init(){
            await setCurrentQnum(1)
            showoutput()
            
            const ele_in = document.getElementsByClassName('inp');
               for (let i=0;i<ele_in.length; i++){
                ele_in[i].style.display="none";
                ele_in[i].addEventListener("keyup", function(event){
                    event.preventDefault();
                    if(event.keyCode===13){
                        showoutput();
                    }
                });
               }
            getidlist()
            
            //console.log(idsList)
    
        
            }
    
            const getidlist =async () =>{
                var xhttp = new XMLHttpRequest();
                xhttp.onreadystatechange = async function() {
                  if (this.readyState === 4 && this.status === 200) {
                      //update users
                    await setIdsList(JSON.parse(this.responseText))
                    //await console.log(idsList)
                  }
                };
                xhttp.open("GET", "/ques/ids/"+module, true);
                xhttp.send();
        
            }
    
            const nextQ = async ()=>{
                if(currentQnum<idsList.length){
                    await setCurrentQnum(Number(currentQnum)+1);
               
                   await seekQ()
  
                   
    
                }
              
            }
            const  prevQ =async ()=>{
                if(currentQnum>1){
                    await setCurrentQnum(Number(currentQnum)-1);
               
                    await seekQ()

                }
               
                
            }
            async function seekQ(){
          
                await loadQues()
                setToggleMode(false)
           }
    
            const getcurrentQid = async ()=>{
                if(currentQnum<1){
                    setCurrentQnum(1)
                    return idsList[0]
                }
                if(currentQnum>idsList.length){
                    setCurrentQnum(idsList.length)
                    return idsList[idsList.length-1]
                }
                //console.log(idsList[currentQnum-1])
                return idsList[currentQnum-1]
                
            }
            const loadQues = async () =>{
                let xhttp = new XMLHttpRequest();
                let id = await getcurrentQid();
                //console.log(id)
                if(id===undefined){
                    id={_id:"6024236491a9d413688d29e5"};
                }
                xhttp.onreadystatechange = function() {
                  if (this.readyState === 4 && this.status === 200) {
                      //update users
                    setCurrentQ(JSON.parse(this.responseText))
                    setToggleMode(false)
                  }
                };
                xhttp.open("GET", "/ques/"+id._id, true);
                xhttp.send();
        
            }
           
            const newQ =()=>{
                var xhttp = new XMLHttpRequest();
                xhttp.onreadystatechange = async function() {
                  if (this.readyState === 4 && this.status === 200) {
                        console.log(this.responseText)
                        console.log("successfully created")
                        await getidlist()
                        await loadQues()
                      
                
                  }
                };
                xhttp.open("POST", "/ques/create", true);
                xhttp.setRequestHeader("Content-type", "application/json");
                xhttp.send(JSON.stringify(currentQ)); 
            }
            const delQ = (ID)=>{
                var xhttp = new XMLHttpRequest();
                xhttp.onreadystatechange = async function() {
                  if (this.readyState === 4 && this.status === 200) {
                    console.log(this.responseText)
                    await getidlist()
                    
                    console.log("successfully deleted")
                
                  }
                };
                xhttp.open("DELETE", "/ques/"+ID, true);
                xhttp.setRequestHeader("Content-type", "application/json");
                xhttp.send(); 
            }
            const updateQ =(ID)=>{
                //add module to currentQ
                setCurrentQ({...currentQ, module:module})
                var xhttp = new XMLHttpRequest();
                xhttp.onreadystatechange = function() {
                  if (this.readyState === 4 && this.status === 200) {
                        console.log(this.responseText)
                        alert("successfully updated")
                
                  }
                };
                xhttp.open("PUT", "/ques/"+ID, true);
                xhttp.setRequestHeader("Content-type", "application/json");
                xhttp.send(JSON.stringify(currentQ)); 
            }
           function showoutput(){
               
               const ele_text = document.getElementsByClassName('output');
               for (let i=0;i<ele_text.length; i++){
                ele_text[i].style.display="block";
               }
               const ele_in = document.getElementsByClassName('inp');
               for (let i=0;i<ele_in.length; i++){
                ele_in[i].style.display="none";
               }
           }
           function showinput(){
               
               const ele_text = document.getElementsByClassName('output');
               for (let i=0;i<ele_text.length; i++){
                ele_text[i].style.display="none";
               }
               const ele_in = document.getElementsByClassName('inp');
               for (let i=0;i<ele_in.length; i++){
                ele_in[i].style.display="block";
               }
           }
    
           function handleChangeInput(e){
    
               switch(e.currentTarget.name){
                   case "OPS0":
                    setCurrentQ({...currentQ, OPS:[e.currentTarget.value,currentQ.OPS[1],currentQ.OPS[2],currentQ.OPS[3],currentQ.OPS[4],currentQ.OPS[5]]})
                       break;
                   case "OPS1":
                    setCurrentQ({...currentQ, OPS:[currentQ.OPS[0],e.currentTarget.value,currentQ.OPS[2],currentQ.OPS[3],currentQ.OPS[4],currentQ.OPS[5]]})
                       break;
                   case "OPS2":
                    setCurrentQ({...currentQ, OPS:[currentQ.OPS[0],currentQ.OPS[1],e.currentTarget.value,currentQ.OPS[3],currentQ.OPS[4],currentQ.OPS[5]]})
                    break;
                   case "OPS3":
                    setCurrentQ({...currentQ, OPS:[currentQ.OPS[0],currentQ.OPS[1],currentQ.OPS[2],e.currentTarget.value,currentQ.OPS[4],currentQ.OPS[5]]})
                       break;
                   case "OPS4":                
                   setCurrentQ({...currentQ, OPS:[currentQ.OPS[0],currentQ.OPS[1],currentQ.OPS[2],currentQ.OPS[3],e.currentTarget.value,currentQ.OPS[5]]})
    
                       break;
                   case "OPS5":
                    setCurrentQ({...currentQ, OPS:[currentQ.OPS[0],currentQ.OPS[1],currentQ.OPS[2],currentQ.OPS[3],currentQ.OPS[4],e.currentTarget.value]})
                    break;
                    case "search":
                        setSearchQnum(e.currentTarget.value)
                        break;
                   default:
                       console.log(e.currentTarget.name)
                       console.log(e.currentTarget.value)
                       console.log("setCurrentQ({...currentQ, " + e.currentTarget.name +": '" + e.currentTarget.value+"'})")
                       try {
                        eval("setCurrentQ({...currentQ,"+e.currentTarget.name+": '"+e.currentTarget.value+"'});")
                       }
                       catch(err){
                        // eslint-disable-next-line no-eval
                        eval("setCurrentQ({...currentQ,"+e.currentTarget.name+": '"+e.currentTarget.value.trim()+"'});")
                       }
                    
                    break;
               }
            
            
           }
    
           async function create(){
               setCurrentQ({
                   Q:"",
                   ANS:"",
                   OPS:["","","","","",""],
                   module:module,
               })
               showinput()
               await setToggleMode(true)
           }
         
let initialRender=useRef(true);

           useEffect(()=>{
             init();
            //setTimeout(loadQues,5000) 
            
        },[]);
    
       
    
 useEffect(()=>{
     //console.log(initialRender.current)
     if(initialRender.current===true){
        initialRender.current=false;
     }else{
    //console.log(idsList)
    seekQ()
    setSearchQnum("")
     }
 },[idsList,currentQnum])

        return (
            <div>
                 <div className="w3-container w3-light-grey">
                 <button onClick={toggleMenu} className="w3-button" ><i className="fa fa-bars"></i> </button>
        <button onClick={prevQ} className="w3-button" ><i className="fa fa-arrow-left"></i> </button>
        <button onClick={nextQ} className="w3-button" ><i className="fa fa-arrow-right"></i> </button>
           <button onClick={create} className="w3-button" >New + </button>
           <input id="search" type="text" className="search" name="search" value={searchQnum} onChange={handleChangeInput} />
           <button onClick={()=>{setCurrentQnum(searchQnum)}} className="w3-button"><i className="fa fa-search"></i> search</button>
       </div>
       <div >
           <div className="w3-container">
               <div className="w3-card w3-center">
                   <div className="w3-container W3-center">
                   <span className="w3-badge w3-xxlarge w3-margin-left">{currentQnum}/{idsList.length}</span>
                    <h4 className="w3-animate-zoom" ><span className="output" onClick={showinput}>{currentQ.Q}</span> 
                        <textarea onChange={handleChangeInput} type="text" name="Q" id="" className="inp w3-input" value={currentQ.Q}></textarea>
                    </h4>
                   </div>
                   <hr />
                   <div className="w3-container w3-left-align">
                    <p className="w3-animate-opacity " ><span>A.</span> <span onClick={showinput} className="output">{currentQ.OPS[0]}</span> 
                        <input onChange={handleChangeInput} type="text" name="OPS0" id="" className="inp w3-input " value={currentQ.OPS[0]} />
                    </p>
                    <p className="w3-animate-opacity " ><span>B.</span> <span onClick={showinput} className="output">{currentQ.OPS[1]}</span> 
                        <input onChange={handleChangeInput} type="text" name="OPS1" id="" className="inp w3-input" value={currentQ.OPS[1]}/>
                    </p>
                    <p className="w3-animate-opacity " ><span>C.</span> <span onClick={showinput} className="output">{currentQ.OPS[2]}</span>    
                        <input onChange={handleChangeInput} type="text" name="OPS2" id="" className="inp w3-input" value={currentQ.OPS[2]}/>
                    </p>
                    <p className="w3-animate-opacity" ><span>D.</span><span onClick={showinput} className="output">{currentQ.OPS[3]}</span> 
                        <input onChange={handleChangeInput} type="text" name="OPS3" id="" className="inp w3-input" value={currentQ.OPS[3]} />
                    </p>
                    <p className="w3-animate-opacity">E.<span className="output" onClick={showinput}>{currentQ.OPS[4]}</span>
                        <input onChange={handleChangeInput} type="text" name="OPS4" id="" className="inp w3-input" value={currentQ.OPS[4]}/>
                    </p>
    
                    <p className="w3-animate-opacity" > <span>F.</span><span onClick={showinput} className="output">{currentQ.OPS[5]}</span> 
                        <input onChange={handleChangeInput} type="text" name="OPS5" id="" className="inp w3-input" value={currentQ.OPS[5]} />
                    </p>
                   </div>
                   <div className="w3-container">
                    ANS:<p className="w3-animate-opacity" ><span onClick={showinput} className="output">{currentQ.ANS}</span> 
                        <textarea onChange={handleChangeInput} type="text" name="ANS" id="" className="inp w3-input w3-border" value={currentQ.ANS}/>
                    </p>
                   </div>
                   <hr />
                   <div className="w3-container w3-center w3-dark-grey">
                   {(toggleMode)?<button onClick={newQ} className="w3-button"><i className="fa fa-plus"></i> Create</button>:
                    <button onClick={()=>{updateQ(currentQ._id)}} className="w3-button"><i className="fa fa-pencil"></i> Update</button>}
                    <button onClick={()=>{delQ(currentQ._id)}} className="w3-button w3-red"><i className="fa fa-trash"></i> Delete</button>
                   </div>
    
               </div>
           </div>
       </div>
            </div>
            
        )
}

export default SapQEdit
